<template>
  <span v-if="set[binding]" class="d-inline-block text-center border rounded-pill bg-white fs-6  mx-1 bi bi-telephone-fill" 
    :class="set[binding].includes(state)?'text-dark ':'text-black-50'" 
    @click="onsubmit" style="width:30px; height:30px;"></span>
</template>
<script>
export default {
  props:{
    state:'',
    binding:'',
    mobile:'',
  },
  data(){
    return {
      set:{
        p:['2','22','31','32','33','41','42','43','51','52'],
        d:['2','22','31','32','33','41','42','43','51','52']
      }
    }
  },
  methods:{
    onsubmit(){
      if(this.set[this.binding].includes(this.state)){
         window.location.href='tel:'+this.mobile;
      }else{
         this.$toast({type:'tips',data:{text:'请接单后,才可电话联系'}});
      }
      
    }
  }
}
</script>


