<template>
    <div class="row text-center py-2 text-info">
        <div class="col">
            <div class="border  border-info  rounded p-1" @click="$tourl('user_mobile')"><span class="bi bi-tablet-fill me-1"></span>改手机号</div>
        </div>
        <div class="col">
            <div class="border border-info rounded p-1" @click="$tourl('consult')"><span class="bi bi-bookmark-x-fill me-1"></span>客服中心</div>
        </div>
        <div class="col">
            <div v-if="info[binding].cancel.includes(show.state)" class="border  border-info  rounded p-1"  @click="$emit('onsubid',info[binding].cancel_data)"><span class="bi bi-bookmark-x-fill me-1"></span>取消订单</div>
            <div v-else class="border rounded bg-light text-black-50 p-1"><span class="bi bi-bookmark-x-fill me-1"></span>取消订单</div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    binding:'',
    set:{},
    data:{},
    show:{},
  },
  data(){
    return {
      info:{
        "p":{
          "cancel":['1','2','22','31','32','33'],
          "cancel_data":{url:'/passenger/state',data:{state:'8'}},
        },
        "d":{
          "cancel":['2','22','31','32'],
          "cancel_data":{url:'/driver/state',data:{state:'7'}},
        },
      }
    }
  }
}
</script>

<style>

</style>


