<template>
    <View-group :set="setdata[set.binding]" :binding="set.binding==='driver'?'d':'p'" :show="data.show" :data="data"  @onsubmit="onsubmit"/>
</template>

<script>
import setdata from './trip/setdata.json';
import ViewGroup from './trip/start'
export default {
  components: {
    ViewGroup,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      setdata,
    }
  },
  mounted(){
    console.log(this.data.push);
  },
  methods:{
      onsubmit(key){
         this.$emit('onsubmit',key);
      },
  }
}
</script>



