<template>
  <span v-if="set[binding]" class="d-inline-block text-center border rounded-pill bg-white fs-6  mx-1 bi bi-chat-dots-fill position-relative" 
    :class="set[binding].includes(state)?'text-dark ':'text-black-50'" 
    @click="onsubmit" style="width:30px; height:30px;">
     <span class="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger" v-if="data.prompt && data.prompt.chatstate && data.prompt.chatstate[chatname]>0">{{data.prompt.chatstate[chatname]}}</span>
    </span>
</template>
<script>
export default {
  props:{
    state:'',
    binding:'',
    url:'',
    chatname:'',
    data:'',
  },
  data(){
    return {
      set:{
        p:['2','22','31','32','33','41','42','43','51','52'],
        d:['2','22','31','32','33','41','42','43','51','52']
      }
    }
  },
  methods:{
    onsubmit(){
      if(this.set[this.binding].includes(this.state)){
         this.$tourl(this.url); 
      }else{
         this.$toast({type:'tips',data:{text:'请接单后,才可以发消息'}});
      }
    }
  }
}
</script>


