<template>
    <div>
        <div class="bg-light rounded py-3 px-4 my-2">
            <div class="d-flex justify-content-between align-items-center">
                <div>
                    <span class="fs-6 fw-bold">你的评价会让乘客做的更好</span>
                    <span class="rounded p-1 bg-white border ms-2">匿名</span>
                </div>
                <div></div>
            </div>
            <div class="fs-1 text-center d-flex justify-content-around text-black-50">
                <div  v-for="n in 5" :key="n"  class="bd-highlight">
                    <span v-if="show[set.binding]===0" class="bi bi-star-fill" :class="{'text-warning':n<=star}" @click="onstar(n)"></span>
                    <span v-else class="bi bi-star-fill" :class="{'text-warning':n<=show[set.binding]}" @click="onstar(n)"></span>
                </div>
            </div>
        </div>
        <EvaluateForm v-if="show[set.binding]===0" :star="star" :selected="selected"  :starname="starname" :starlabel="starlabel" @onstar="onstar" @onselect="onselect" @onsubmit="onsubmit" />
        <EvaluateShow v-else v-show="isshow" :star="show[set.binding]" :selected="show[set.binding+'_label']?show[set.binding+'_label'].split(','):[]" :starname="starname" :starlabel="starlabel" @isActive="isshow=false" />
    </div>
</template>

<script>
import EvaluateForm from "./trip-evaluate-form";
import EvaluateShow from "./trip-evaluate-show";
import axios from 'axios'
export default {
  components: {
    EvaluateForm,
    EvaluateShow,
  },
  props: {
    set:{},
    data:{},
    show:{},
  },
  data(){
      return {
          star:0,
          starname:['未评','非常不满意,各方面都很差','不满意,比较差','一般,还需发送','比较满意,仍可改善','非常满意,无可挑剔'],
          starlabel:[
              {'1':'车况较差','2':'态度较差','3':'道路不熟','4':'临时加价','5':'迟到到达','6':'危险驾驶'},
              {'7':'车内干净','8':'文明守礼','9':'路线熟悉','10':'不多收费用','11':'按约定等候','12':'驾驶技术优秀'},
          ],
          selected:[],
          isshow:false,
          loading:false,
      }
  },
  methods:{
    onstar(n){
        if(this.show[this.set.binding]===0){
        this.star=n;
        this.selected=[];
        }else{
        this.isshow=true;
        }
    },
    onselect(key){
        //重新渲染
        if(this.selected.includes(key)){
            this.selected=this.selected.filter(function (ele){return ele != key;});
        }else{
            this.selected.push(key);
        }
    },
    onsubmit(){
         this.$emit('onsubmit',{method: 'post',url:this.set.accept,params:{token:this.$store.state.token},data:{id:this.show.id,euid:this.show[this.set.binding_uid],evaluate:this.star,evaluate_label:this.selected.join(',')}});
    },
  }
}
</script>



