<template>
  <div>
      <com-layout-title :title="data.user.nickname"/>
      <div class="d-flex justify-content-between align-items-center bg-info bg-gradient text-white fs-normal p-2" v-if="data.trip">
        <div class="bd-highlight">
          <div class="fs-normal">{{data.trip.setdate|setdate(data.trip.setdate_end)}} | {{item.state|tripstate}}</div>
          <div>
            <span>{{data.trip.origin}}</span>
            <span class="bi bi-arrow-right-short"></span>
            <span>{{data.trip.destination}}</span>
          </div>
        </div>
        <div class="bd-highlight" v-if="data.push[3]">
            <com-trip-mobile :mobile='data.user.mobile' :binding="data.push[3]" :state="data.trip.state"/>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  components: { 
  },
  props: {
    set:{},
    data:{},
  }
}
</script>


